<script>
  import api from './api';
  import SmsCode from '../../../../account/components/smsCode';

  export default {
    components: {
      SmsCode
    },
    data() {
      return {
        formData: {}
      };
    },
    mounted() {
      this.loadData();
    },
    methods: {
      loadData() {
        api.detail({ id: this.$store.state.user.enter_team_id },
          res => {
            this.formData = res.data.data;
          });
      },
      submit() {
        api.save(this.formData, res => {
          this.$router.push({name:'teamInfo'})
          }
        );
      }
    }
  };
</script>
